$not-big: 768px;
$big: 1280px;

$breakpointz: (
  'not-big': $not-big,
  'big': $big
) !default;


@mixin less-than($breakpoint) {
  @if map-has-key($breakpointz, $breakpoint) {
    @media (max-width: map-get($breakpointz, $breakpoint)) {
      @content;
    }
  }
}

@mixin greater-than($breakpoint) {
  @if map-has-key($breakpointz, $breakpoint) {
    @media (min-width: map-get($breakpointz, $breakpoint)) {
      @content;
    }
  }
}

.sfds-layout-container {
  .sfds-responsive-container {
    padding-left: 10%;
    padding-right: 10%;
    &.sfds-responsive-container-right-zero {
      padding-right: 0;
    }
  }
}

header {
  padding: 40px 5%;
  .sfgov-nav-container {
    .sfgov-nav-container__content {
      @include clearfix;
      .sfgov-menu-btn {
        display: none;
      }
      @include less-than('not-big') {
        .sfgov-menu-btn {
          display: block;
          float: right;
          margin: 0;
          padding: 0;
          background: transparent;
          color: #5168E4;
          width: auto;
        }
      }
      .sfgov-logo__container {
        float: left;
        width: 24%;
        padding: 0;
        margin: 0;
      }
      .head-right--container {
        float: right;
        width: 75%;
        text-align: right;
        > * {
          vertical-align: middle;
        }
        nav {
          display: inline-block;
          .sfgov-nav-inner {
            .sfgov-nav-close {
              display: none;
            }
            .sfgov-main-navigation {
              .menu {
                padding: 0;
                margin: 0;
                list-style-type: none;
                > li {
                  display: inline-block;
                  margin-right: 25px;
                  a {
                    color: #5168e4;
                    text-decoration: none;
                  }
                }
              }
            }
          }
        }
        #block-gtranslate {
          display: inline-block;
          margin-right: 25px;
          .gtranslate {
            background: url(../images/icons/translate-2.svg) no-repeat center left;
            > select {
              border: 0;
              appearance: none;
              padding: 0 0 0 25px;
              margin: 0;
              height: auto;
              width: 100px;
              background: none;
              color: #5168E4;
            }
          }
        }
        @include less-than('not-big') {
          nav.sfgov-nav {
            display: none;
          }
          #block-gtranslate {
            display: none;
          }
        }
      }
    }
  }
}

.sfgov-mobile_nav-active {
  @include less-than('not-big') {
    header {
      .sfgov-nav-container {
        .sfgov-nav-container__content {
          .head-right--container {
            nav.sfgov-nav {
              display: block;
              position: absolute;
              left: 0;
              width: 100%;
              height: 100%;
              text-align: left;
              padding-left: 60px;
              background-color: rgba(0,0,0,.7);
              .sfgov-nav-inner {
                width: 100%;
                height: 100%;
                background: #EDF4F7;
                .sfgov-main-navigation {
                  width: 100%;
                  .menu {
                    li {
                      display: block;
                      margin: 0;
                      line-height: initial;
                      a {
                        padding: 20px;
                        margin: 0 25px;
                        display: block;
                        border-bottom: 1px solid #1C3E57;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}